import React from "react";
import { connect } from "react-redux";
import * as Actions from "../../store/Feature1/FeatureAction";
import { bindActionCreators } from "redux";
import TableComponent from "../../components/Controls/Table/Table";
import ReactGA from "react-ga";

class JdList extends React.Component {
  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    this.props.FeatureAction.actionRequest();
  }

  handlePagination = (e) => {
    this.setState({ page: e.selected + 1 });
  };
  render() {
    let inboundDatacolumn = [
      {
        Header: "NO",
        accessor: "no",
        minWidth: 100,
      },
      {
        Header: "First Name",
        accessor: "pname",
        minWidth: 100,
      },
      {
        Header: "Last Name",
        accessor: "lname",
        minWidth: 100,
      },
      {
        Header: "Patient DOB",
        accessor: "patientdob",
        minWidth: 100,
      },
    ];
    let inboundData = [];
    inboundData.push({
      no: 1,
      pname: "ksjghdj",
      lname: "ksjfjgkd",
      patientdob: "hfdbj",
    });
    inboundData.push({
      no: 2,
      pname: "ksjghdj",
      lname: "ksjfjgkd",
      patientdob: "hfdbj",
    });

    return (
      <div className='container my-5'>
        <div>
          <TableComponent
            data={inboundData}
            columns={inboundDatacolumn}
            handlePageClick={(e) => {
              this.handlePagination(e);
            }}
            totalPage={3}
          />
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    FeatureReducer: state.FeatureReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    FeatureAction: bindActionCreators(Actions, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(JdList);
